<template>
  <div>
    <div>
      <a-input placeholder="" v-model="queryParam.title" />
      <mavon-editor
        v-model="queryParam.originalContent"
        ref="md"
        @change="change"
        style="min-height: 600px;z-index: 1;"
      />
      <div class="article-bottom">
        <div class="article-option">
          <a-button type="primary" @click="showDrawer"></a-button>
        </div>
      </div>

      <a-drawer
        title=""
        placement="right"
        :closable="false"
        @close="onClose"
        :visible="visible"
        width="30rem"
      >
        <a-form>
          <a-form-item label="" help=",">
            <a-input placeholder="" v-model="queryParam.viewName" />
          </a-form-item>

          <a-form-item label="">
            <a-select style="width: 100%" v-model="queryParam.templateId">
              <a-select-option
                :value="item.id"
                v-for="item in templates"
                :key="item.id"
              >{{item.name}}</a-select-option>
            </a-select>
          </a-form-item>

          <a-form-item label="">
            <a-select
              allowClear
              mode="tags"
              style="width: 100%"
              @blur="handleBlur"
              placeholder="Tags Mode"
              v-model="selectedTagNames"
            >
              <a-select-option
                v-for="item in tags"
                :key="item.id+''"
                :value="item.name"
              >{{item.id}}-{{item.name}}</a-select-option>
            </a-select>
          </a-form-item>

          <a-form-item label="">
            <a-tree
              checkable
              :defaultExpandAll="true"
              :checkedKeys="queryParam.categoryIds"
              @check="onCheck"
              :treeData="categorys"
            />
          </a-form-item>

          <a-form-item label="">
            <a-radio-group v-model="queryParam.haveHtml" defaultValue="1">
              <a-radio :value="1"></a-radio>
              <a-radio :value="0"></a-radio>
            </a-radio-group>
          </a-form-item>

          <a-form-item>
            <a-button @click="submit"></a-button>
            <a-button @click="save"></a-button>
          </a-form-item>
        </a-form>
      </a-drawer>
    </div>
  </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import tagsApi from "@/api/tags.js";
import categoryApi from "@/api/category.js";
import templateApi from "@/api/template.js";
import axios from "axios";
import articleApi from "@/api/article.js";
export default {
  // 
  components: {
    mavonEditor
  },
  data() {
    return {
      queryParam: {
        originalContent: "", // markdown
        tagIds: [],
        categoryIds: [],
        haveHtml: 0,
        templateId: '',
        title: "",
        viewName: "",
        summary: "",
        status: "PUBLISHED"
      },
      visible: false,
      tags: [],
      selectedTagNames: [],
      selectedTagIds: [],
      categorys: [],
      selectCategoryIds: [],
      templates: [],
      isUpdate: false
    };
  },
  computed: {
    tagIdMap() {
      const tagIdMap = {};
      //tagsid map
      this.tags.forEach(tag => {
        tagIdMap[tag.id] = tag;
      });
      return tagIdMap;
    },
    tagNameMap() {
      const tagNameMap = {};
      //tags name map
      this.tags.forEach(tag => {
        tagNameMap[tag.name] = tag;
      });

      return tagNameMap;
    }
  },
  watch: {
    // tags(newValue) {
    //   // tags
    //   if (newValue) {
    //     if (this.selectedTagIds.length != 0) {
    //       this.selectedTagNames = this.selectedTagIds.map(
    //         tagId => this.tagIdMap[tagId].name
    //       );
    //     }
    //   }
    // }
  },
  beforeRouteEnter(to, from, next) {
    // Get post id from query
    const articleId = to.query.articleId;
    next(vm => {
      if (articleId) {
        articleApi.findById(articleId).then(response => {
          const article = response.data.data;
          // console.log(article);
          vm.queryParam.originalContent = article.originalContent; // markdown
          // vm.queryParam.haveHtml= article.haveHtml
          // vm.queryParam.templateId = article.templateId;
          vm.queryParam.title = article.title;
          vm.queryParam.viewName = article.viewName;
          vm.queryParam.summary = article.summary;
          vm.queryParam.status = article.status;

          //     tagIds: []
          // categoryIds: []
          vm.isUpdate = true;
        });
      }
    });
  },
  methods: {
    // 
    change(value, render) {
      // render  markdown [html]
      this.queryParam.formatContent = render;
    },
    // 
    submit() {
      if (!this.queryParam.title) {
        this.$notification["error"]({
          message: "!!"
        });
        return;
      }
      if(!this.queryParam.templateId){
        this.$notification["error"]({
          message: "!!"
        });
        return
      }
      if (this.isUpdate) {
        articleApi
          .update(this.$route.query.articleId, this.queryParam)
          .then(response => {
            // console.log(response);
            const data = response.data.data;
            if (data.haveHtml) {
              this.$notification["success"]({
                message: ":"+response.data.message+"html"+data.viewName
              });
            }else{
              this.$notification["success"]({
                message:":"+ response.data.message+"html"
              });
            }

            // this.$router.push("/article/list");
          });
      } else {
        // console.log(this.queryParam);
        articleApi.create(this.queryParam).then(response => {
          // console.log(response);
          const data = response.data.data;
          if (data.haveHtml) {
              this.$notification["success"]({
                message: response.data.message+"html"+data.viewName
              });
            }else{
              this.$notification["success"]({
                message: response.data.message+"html"
              });
            }
        });
      }
      this.$router.push("/article/list");
    },
    save() {},

    showDrawer() {
      this.loadTags();
      this.loadcategory();
      this.loadTempalte();
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    /**tagsid */
    handleBlur() {
      const tagNamesToCreate = this.selectedTagNames.filter(
        tagName => !this.tagNameMap[tagName]
      );
      // console.log(tagNamesToCreate);
      // console.log(tagNamesToCreate.length);
      if (tagNamesToCreate.length == 0) {
        this.queryParam.tagIds = this.selectedTagNames.map(
          tagName => this.tagNameMap[tagName].id
        );
        // If empty
        return;
      }
      const createPromises = tagNamesToCreate.map(tagName =>
        tagsApi.createWithName(tagName)
      );
      axios.all(createPromises).then(
        axios.spread(() => {
          this.loadTags(() => {
            this.queryParam.tagIds = this.selectedTagNames.map(
              tagName => this.tagNameMap[tagName].id
            );
          });
        })
      );
    },
    handleChange(value) {
      this.queryParam.templateId = value;
      // console.log(value);
    },
    loadTags(callback) {
      tagsApi.list().then(response => {
        this.tags = response.data.data;
        if (callback) {
          callback();
        }
        // console.log(response.data.data);
      });
    },
    loadcategory() {
      categoryApi.list().then(response => {
        this.categorys = categoryApi.concreteTree(response.data.data);
      });
    },
    loadTempalte() {
      templateApi.findByType("ARTICLE").then(response => {
        this.templates = response.data.data;
        // console.log(response);
      });
    },
    onCheck(checkedKeys, e) {
      // console.log("onCheck", checkedKeys);
      // console.log("e::", e);
      // this.categoryIds = checkedKeys;
      this.queryParam.categoryIds = e.checkedNodes
        .filter(node => {
          return node.data.props.isLeaf;
        })
        .map(node => node.key);

      // console.log(this.queryParam.categoryIds);
    }
  }
};
</script>

<style>
.article-bottom {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: 4rem;
  background: #fff;
  z-index: 5;
}
.article-option {
  float: right;
  position: relative;
  top: 1rem;
  right: 2rem;
}
</style>